import { Component, OnInit, ViewChildren, QueryList, ElementRef, AfterViewInit } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { Services } from '../../models/services';
import { LanguageService } from 'src/app/services/language.service';

@Component({
  selector: 'sp-services',
  templateUrl: './services.component.html',
  styleUrls: ['./services.component.less']
})
export class ServicesComponent implements OnInit, AfterViewInit {
  @ViewChildren('imagesRef') imagesRef?: QueryList<ElementRef>;

  selectedLanguage = 0;
  selectedService = undefined;
  serviceImages = [];
  services = Services;
  hoveredImg = undefined;
  selectedImg = undefined;
  showOnlySix = true;
  imagesLoaded = false;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private languageService: LanguageService
  ) {}

  ngOnInit() {
    this.initSelectedServiceObserver();
    this.languageService.languageToggle.subscribe(language => this.selectedLanguage = language);
  }

  ngAfterViewInit() {
    const imageLoadCount = this.getServiceImgLoadCount();
    this.asyncImageLoad(imageLoadCount);
  }

  selectService = (service): void => { this.router.navigateByUrl(service.link); };

  exitSpotlight = (): void => { this.selectedImg = undefined; };

  feedNextImage(direction: string): void {
    if (direction === 'left') this.selectedImg = this.getImageToTheLeft();
    if (direction === 'right') this.selectedImg = this.getImageToTheRight();
  }

  showMore(): void {
    this.showOnlySix = false;
    const imgLoadCount = this.selectedService.images.length;
    setTimeout(() => this.asyncImageLoad(imgLoadCount), 100);
  }

  private getServiceImgLoadCount(): number {
    return this.selectedService.images.length > 6 ? 6 : this.selectedService.images.length;
  }

  private getImageToTheLeft(): string {
    let indexToReturn;
    const imgIndex = this.selectedService.images.findIndex(img => img === this.selectedImg);
    const maxIndex = this.selectedService.images.length - 1;

    if (imgIndex === 0) {
      indexToReturn = maxIndex;
    } else {
      indexToReturn = imgIndex - 1;
    }

    return this.selectedService.images[indexToReturn];
  }

  private getImageToTheRight() {
    let indexToReturn;
    const imgIndex = this.selectedService.images.findIndex(img => img === this.selectedImg);
    const maxIndex = this.selectedService.images.length - 1;

    if (imgIndex === maxIndex) {
      indexToReturn = 0;
    } else {
      indexToReturn = imgIndex + 1;
    }

    return this.selectedService.images[indexToReturn];
  }

  private initSelectedServiceObserver(): void {
    this.route
      .paramMap
      .subscribe((params: ParamMap) => {
        this.showOnlySix = true;
        let id: string | number = params.get('id');
        if (!id) id = 1;
        this.selectedService = this.services.find(s => s.id === +id);
        const imageLoadCount = this.getServiceImgLoadCount();
        setTimeout(() => this.asyncImageLoad(imageLoadCount), 50);
      });
  }

  private asyncImageLoad(loadCount: number): void {
    let count = 0;
    
    this.imagesRef.forEach(imgRef => {
      const imgHolder = imgRef.nativeElement;
      const imageUrl = imgHolder.getAttribute('data-src');
      const imageWidth = imgHolder.getAttribute('data-img-width');

      const img = new Image();
      img.src = imageUrl;
      img.style.borderRadius = '5px';
      img.style.width = imageWidth ? `${imageWidth}%` : '95%';
      img.style.animation = 'fade-in .5s';

      img.onload = () => {
        imgHolder.innerHTML = '';
        imgHolder.appendChild(img);
      };
      

      ++count;
      this.imagesLoaded = count === loadCount;
    });
  }
}
