import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';
import { Language } from '../models/Languages';

@Injectable({
  providedIn: 'root'
})
export class LanguageService {
  languageToggle = new Subject<Language>();
  private selectedLanguage = this.getFromLocalStorage();

  toggleLanguage(language: Language): void {
    this.saveInLocalStorage(language);
    this.selectedLanguage = language;
    this.languageToggle.next(language);
  }

  getSelectedLanguage = (): Language => this.selectedLanguage;

  private saveInLocalStorage(language: Language): void {
    window.localStorage.setItem('language', language.toString());
  }

  private getFromLocalStorage(): Language {
    const language = window.localStorage.getItem('language');
    if (language == Language.Spanish.toString()) return Language.Spanish;
    return Language.English;
  }
}
