import { Component, OnInit } from '@angular/core';
import { LanguageService } from 'src/app/services/language.service';

@Component({
  selector: 'sp-join-the-team',
  templateUrl: './join-the-team.component.html',
  styleUrls: ['./join-the-team.component.less']
})
export class JoinTheTeamComponent implements OnInit {
  selectedLanguage = 0;

  constructor(private languageService: LanguageService) { }

  ngOnInit() {
    this.languageService.languageToggle.subscribe(language => this.selectedLanguage = language);
  }
}
