import { Component, OnInit } from '@angular/core';
import { LanguageService } from 'src/app/services/language.service';
import { Subscription } from 'rxjs';
import { Language } from 'src/app/models/Languages';

@Component({
  selector: 'sp-aboutus',
  templateUrl: './aboutus.component.html',
  styleUrls: ['./aboutus.component.less']
})
export class AboutusComponent implements OnInit {
  languageToggle$: Subscription;
  selectedLanguage;
  aboutUsMessage = ' M&JI Paradise is a local cleaning service company in Houston, TX. We are proud Texans offering a plenitude of cleaning services to our fellow Texans. Let us help you clean up!'
  contactUsMessage = 'Feel free to <a class="faux-link" [routerLink]="[\'/contactus\']">contact us</a> with any questions you may have.';

  constructor(private languageService: LanguageService) { }

  ngOnInit() {
    this.toggleLangugae(this.languageService.getSelectedLanguage());
    this.languageService.languageToggle.subscribe(language => this.toggleLangugae(language));
  }

  private toggleLangugae(language: Language) {
    this.selectedLanguage = language;

    switch (language) {
      case Language.English:
        this.aboutUsMessage = ' M&JI Paradise is a local cleaning service company in Houston, TX. We are proud Texans offering a plenitude of cleaning services to our fellow Texans. Let us help you clean up!'
      break;
      case Language.Spanish: 
        this.aboutUsMessage = 'M&JI Paradise es una compañia de servicio de limpieza orgullosamente Tejana sirviendo a la comunidad de Houston. ¡Dejanos echarte la mano limpiando!';
      break;
    }
  }
}
